.details{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-decoration: none;
    padding-top:100px;
}
.All-cards{
    width:280px;
    background-color: rgb(255, 255, 255);
    margin:25px;
    border-radius: 10px;
    box-shadow:3px 3px 3px 3px rgb(194, 193, 193);
    
}
.All-cards:hover{
    box-shadow:2px 2px 2px 2px gray;
}
.country-flag{
    width:280px;
    height:170px;
    border-radius: 10px;
}
.allcountries-contents{
    margin-left:20px;
    font-weight: 400;
    text-decoration: none;
}
.allcountries-data{
    font-size: 18px;
    font-weight: 600;
    text-decoration: none;
}
.allcountries-contents:nth-last-child(1){
    padding-bottom:15px;
}
@media screen and (max-width:617px){
    .All-cards{
        margin-left:0;
        margin-right: 0;
        width:100%;
        justify-content: center;
    }
    .country-flag{
        width:320px;
        height:220px;
    }
    .allcountries-contents{
        margin-left:60px;
    }
}