.nav-items{
    width:100%;
    height:60px;
    display:flex;
    justify-content: space-between;
    /* position:fixed; */
    box-shadow: 2px 2px 2px 2px #ebe9e9;
    background: #fff;
    flex-wrap: wrap;

}
.text-nav{
    margin-left:20px;
}
.search-text{
    height:30px;
    width:200px;
    margin:15px;
    border:none;
}
.dropdown-btn{
    height:30px;
    text-align: center;
    font-weight: 100;
}
.nav-right{
    margin-right:20px;
}
.back-btn{
    margin-left:20px;
    margin-top: 20px;
    font-size: 20px;
    box-shadow: 1px 1px 1px 1px rgb(191,189,189);
    height:28px;
    padding:7px;
    border-radius: 10;
}
@media screen and (max-width:617px){
    .nav-items{
        display: block;
        margin:0;
    }
    .text-nav{
        margin-left:0;
        margin-right: 0;
        text-align: center;
        font-size: 25px;
        font-weight: 800;
    }

    .nav-right{
        text-align: center;
    }
    .search-text{
        height:40px;
        width:65%;
        margin:15px;
        box-shadow: 1px 1px 1px 1px rgb(196, 193, 193);
    }
    .search-text:placeholder-shown{
        font-weight: 700;
    }
    .dropdown-btn{
    height:40px;
    background:white;
    text-align: center;
    font-weight: 500;
    outline:none;
    box-shadow: 1px 1px 1px 1px rgb(196, 193, 193);
    }
    .back-btn{
        margin-left:10px;
        margin-top: 15px;
        font-size: 18px;
        box-shadow: 1px 1px 1px 1px rgb(204, 117, 117);
        height:22px;
        padding:5px;
        border-radius: 15;
    }
}